/**
 * CSS
 ============================================================================ */
import "./Appetizer.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation, Trans } from "react-i18next";

/**
 * User imports
 ============================================================================ */
import InjectChildren from "../injectChildren";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "Appetizer",
    style: {},
    title: "Untitled",
    muteTitle: false,
    compact: false,
    href: "#",
    linkText: "more...",
    showContent: true,
    onLinkClick: () => {},
    onLinkMouseDown: () => {},
    transKey: "JobAppetizers:Engineering Appetizer",
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */

  /**
   * Return
   ============================================================================ */
  let title_class = props.compact ? "compact " : "";
  title_class += props.muteTitle ? "text-muted " : "";
  const p_class = props.compact ? "compact" : "";
  return (
    <div className="appetizer" style={props.style}>
      {props.muteTitle ? (
        <p className={title_class}>{props.title}</p>
      ) : (
        <h4 className={title_class}>{props.title}</h4>
      )}

      {props.showContent && (
        <p className={p_class}>
          <Trans i18nKey={props.transKey}>
            <InjectChildren {...args} />
          </Trans>
        </p>
      )}
      {props.href !== false && (
        <p className={p_class}>
          <a
            href={props.href}
            onClick={props.onLinkClick}
            onMouseDown={props.onLinkMouseDown}
          >
            {props.linkText}
          </a>
        </p>
      )}
    </div>
  );
};
